const BlogPosts = ({ posts }) => {
  if (!posts) return null
  return (
    <div>
      {posts.map(post => {
        return (
          <div key={post.node._meta.id}>
            <h2>{RichText.asText(post.node.title)}</h2>
            <img fluid={post.node.hero_image.url} alt={post.node.hero_image.alt}/>
          </div>
        )
      })}
    </div>
  )
}

export default ({ data }) => {
  const posts = data.prismic.allArchives.edges

  return (
    <Layout>
      <SEO title="Archive" />
      <div>
        <h1>All Archives</h1>
      </div>
      <BlogPosts posts={posts} />
    </Layout>
  )
}


export const pageQuery = graphql`
  query ArchiveIndex {
    prismic {
      allArchives {
        edges {
          node {
            title
            hero_image
            hero_imageSharp {
              id
              uid
              childImageSharp {
                id
                fluid {
                  base64
                  tracedSVG
                  srcWebp
                  srcSetWebp
                  originalImg
                  originalName
                  presentationWidth
                  presentationHeight
                  srcSet
                  src
                  sizes
                }
              }
            }
            _meta {
              id
            }
          }
        }
      }
    }
  }
`
